<template>
  <div class="box">
    <ul class="aaa">
      <li v-for="(item,i) in arr2" :key="i" @touchstart="down(),now(item.inde)">
        <img :src="item.imgs" />
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  data() {
    return {
      id: "",
      urls: "",
      inde: 0 /*  */,
      hua: 0,
      num: 0,
      obj: {},
      arr2: [],
      postdata: {}
    };
  },
  methods: {
    now(i) {
      this.inde = i;
    },
    down() {
      var that = this;
      /* 上滑代逻辑码 */
      var aa = document.getElementsByClassName("aaa")[0].firstChild;
      if (Math.abs(aa.getBoundingClientRect().top) < 100) {
        if (that.hua == 0) {
          alert("当前是第一章");
        } else {
          var brr = [];
          that.urls = that.data[+that.hua - 1].rule;
          for (var j = 0; j < that.data[+that.hua - 1].end_num; j++) {
            var turl =
              "https://mhpic.manhualang.com" +
              that.data[+that.hua - 1].rule +
              "-mht.middle.webp";
            var tsrc = turl.replace(/\$\$/gi, j + 1);
            obj = {
              imgs: tsrc,
              inde: j
            };
            brr.push(obj);
          }
          that.arr2 = brr.concat(that.arr2);
          that.arr2 = that.arr2.slice(
            0,
            that.data[+that.hua - 1].end_num + that.num + 1
          );
          that.num = that.data[+that.hua - 1].end_num;
          that.hua--;
          var tchange = document.getElementsByClassName("aaa")[0].children;
          window.scroll(0, tchange[that.num].getBoundingClientRect().top);
        }
      }
      /*控制下滑的逻辑代码*/
      var bb = document.getElementsByClassName("aaa")[0].lastChild;
      if (
        bb.getBoundingClientRect().bottom <
        document.documentElement.clientHeight + 10
      ) {
        if (that.hua == that.postdata.max - 1) {
          alert("当前是最后一章");
        }else{
          that.urls = that.data[that.hua+1].rule;
          for (var i= 0; i < that.data[+that.hua + 1].end_num; i++) {
            var obj = {};
            var urlss ="https://mhpic.manhualang.com" + that.data[+that.hua + 1].rule + "-mht.middle.webp";
            var srcs = urlss.replace(/\$\$/gi, i + 1);
            obj = {
              imgs: srcs,
              inde: i
            };
            that.arr2.push(obj);
          }
          that.arr2 = that.arr2.slice(
            -(that.data[+that.hua + 1].end_num + that.num)
          );
          window.scroll(0, 0);
          var change = document.getElementsByClassName("aaa")[0].children;
          var dis = Math.abs(change[that.num - 1].getBoundingClientRect().top);
          window.scroll(0, dis);
          that.num = that.data[+that.hua + 1].end_num;
          that.hua++;
        }
      }
    }
  },
  created() {
    var that = this;
    var data = that.$route.query.postdata;
    if (typeof data == "string") {
      data = JSON.parse(data);
    }
    that.postdata = data;
    var urls = "https://mhpic.manhualang.com" + data.urls + "-mht.middle.webp";
    that.id = data.id;
    that.urls=data.urls
    var gurl =
      "https://www.manhuatai.com/api/getComicInfoBody?comic_id=" + that.id;
    that.hua = data.hua;
    that.num = Number(data.num);
    for (var i = 0; i < data.num; i++) {
      var obj = {};
      var src = urls.replace(/\$\$/gi, i + 1);
       obj = {
        imgs: src,
        inde: i
      };
      that.arr2.push(obj);
    }
    that.axios.get(gurl).then(function(res) {
      var shuju = res.data.data.comic_chapter;
      shuju = shuju.reverse();
      that.data = shuju;
      var scro = document.getElementsByClassName("aaa")[0].children;
      window.scroll(0, scro[data.inde].getBoundingClientRect().top);
    });
  },
  mounted() {
    const mui= window.mui
      mui.init({
                swipeBack: false, //关闭右滑关闭功能(默认就是false)
                keyEventBind: {
                    backbutton: true //开启back按键监听( 默认就是true)
                }
      });
        // //首页返回键处理
        // //处理逻辑：1秒内，连续两次按返回键，则退出应用；
        var first = null;
      mui.back = function() {
       //首次按键，提示 再按一次退出应用
       if (!first) {
            first = new Date().getTime(); //记录第一次按下回退键的时间
            mui.toast("再按一次退出应用"); //给出提示
            history.go(-1); //回退到上一页面
           setTimeout(function() {
              //1s中后清除
            first = null;
             }, 1000);
           } else {
             if (new Date().getTime() - first < 1000) {
             //如果两次按下的时间小于1s，
              window.plus.runtime.quit(); //那么就退出app
             }
           }
       }
  },
  beforeDestroy() {
    var that = this;
    var arr = localStorage.collection;
    if (typeof(arr) != "undefined" && arr.length > 2) {
      arr = JSON.parse(arr);
    } else {
      arr = []; /* 判断是否有localstorage存在，有就转换格式，没有就创建成数组 */
    }
    var index = -1; /* index这个用于查看该漫画在书架中的位置 */
    for (var i = 0; i < arr.length; i++) {
      if (arr[i].id == that.id) {
        index = i; /* 获取该漫画在本地存储中的位置 */
      }
    }
    if (index >= 0) {
      /* index>=0说明该漫画已经收藏了 */
      /* 如果已经收藏就可以直接改看到那个章节 */
      arr[index].urls = that.urls;
      arr[index].hua = that.hua;
      arr[index].num = that.num;
      arr[index].inde = that.inde;
    } else {
      /* else就是没收藏 */
      var obj = sessionStorage.current;
      if (obj.length > 2) {
        /* 详情页面过来的 并且没有收藏*/
        var obj1 = {
          urls:that.urls,
          hua:that.hua,
          num:that.num,
          inde:that.inde
        };
        obj = JSON.parse(obj);
        obj = Object.assign(obj,obj1);
        arr.push(obj);
      }
    }
    arr = JSON.stringify(arr);
    localStorage.setItem("collection",arr);
  }
};
</script>
<style scoped>
.box {
  width: 100%;
}
ul li img {
  width: 100%;
}
</style>